import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import svijany from '../../assets/images/svijany-gallery.png';
import svijanyGirl from '../../assets/images/svijany-girl.jpg';
import svijanyGirl2 from '../../assets/images/svijany-girl2.jpg';
import budweiserGirl from '../../assets/images/budweiser-girl.jpg';
import bakalar from '../../assets/images/bakalar-beers.jpg';
import annaMaria from '../../assets/images/beer-bouquet.jpg';
import kozel from '../../assets/images/kozel.jpg';
import './Gallery.scss';

class Gallery extends Component {
  render() {
    return (
      <div className="gallery">
        <br />
        <br />
        <br />
        <h1>{!this.props.isBg ? "Gallery" : "Галерия"}</h1>
        <div className="images">
          <img src={svijanyGirl} alt="svijany beer" width="98%"/>
          <br />
          <img src={svijany} alt="svijany beer" width="98%"/>
          <br />
          <img src={annaMaria} alt="bakalar beer" width="98%"/>
          <br />
          <img src={bakalar} alt="bakalar beer" width="98%"/>
          <br />
          <img src={budweiserGirl} alt="bakalar beer" width="98%"/>
          <br />
          <img src={kozel} alt="kozel beer" width="98%"/>
          <br />
          <img src={svijanyGirl2} alt="svijany beer" width="98%"/>
          <br />
        </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Gallery);
